.mainBlock {
    height: 100vh;
    background-color: #1f1f20;
}

.greeting {
    width: 300px;
    text-align: left;
}

.particles {

}

.greeting h1 span {
    position: relative;
    z-index: 0;
    display: inline-block;
}

.greeting h1 span::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 20px;
    background-color: #4e93e6;
    bottom: 0;
    z-index: -1;
}


.photo {
    position: relative;
    z-index: 1;
}

.img {
    width: 300px;
    height: 400px;
    background-size: cover;
    background-position: 00% 0;
}

.img::before {
    content: '';
    position: absolute;
    height: 110%;
    width: 105%;
    border: 5px solid #4e93e6;
    left: 15px;
    top: -15px;
    z-index: -1;
}

.greeting span {
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 16px;
}

.title {
    font-family: 'Josefin Sans', sans-serif;
    color: #fff;
    padding: 10px 0 10px;
    letter-spacing: 2px;
    margin: 0;
    box-sizing: border-box;
    font-size: 40px;
}

.title span {
    display: inline-block;
    font-size: 40px;
}

.greeting p {
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 27px;
}


